@font-face {
  font-family: 'IBM Plex Mono';
  src: url('./fonts/IBMPlexMono-Regular.woff2') format('woff2'),
    url('./fonts/IBMPlexMono-Regular.woff') format('woff');
}
@font-face {
  font-weight: 700;
  font-family: 'IBM Plex Mono';
  src: url('./fonts/IBMPlexMono-Bold.woff2') format('woff2'),
    url('./fonts/IBMPlexMono-Bold.woff') format('woff');
}
@font-face {
  font-style: italic;
  font-family: 'IBM Plex Mono';
  src: url('./fonts/IBMPlexMono-Italic.woff2') format('woff2'),
    url('./fonts/IBMPlexMono-Italic.woff') format('woff');
}
@font-face {
  font-weight: 700;
  font-style: italic;
  font-family: 'IBM Plex Mono';
  src: url('./fonts/IBMPlexMono-BoldItalic.woff2') format('woff2'),
    url('./fonts/IBMPlexMono-BoldItalic.woff') format('woff');
}

html {
  /* overflow: hidden; */
  font-variant-numeric: slashed-zero;
  font-family: sans-serif;
  font-size: 14px;
  font-family: 'IBM Plex Mono';
  line-height: 1.5;
  background: #444;
  color: #eee;
}

body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  text-decoration: underline;
  cursor: pointer;
  color: #eee;
}

button:focus {
  outline: none;
  background: yellow;
}

/* Uses javascript in index.html. from https://stackoverflow.com/a/51093815/8691291 */
.using-mouse button:focus {
  background: transparent;
}

button:active,
.using-mouse button:active,
.active {
  color: #111;
  background: #eee;
  text-decoration: none;
}
